import React from 'react';
import styled from 'styled-components';
import Icon from "./Icon";
import {useSpring, animated} from "react-spring";
import uniqueId from "../Functions/uniqueId";

const Title = styled.div`
    font-size: 16pt;
    padding: 8px;
    position: relative;
    display: flex;
    
    :before {
        display: block;
        content: '${props => props.show ? '-' : '+'}';
        position: absolute;
        line-height: 0;
        right: 0;
        width: 16px;
        text-align: center;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
`;

const Movie = styled.video`
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
`;

const Wrapper = animated(styled.div`
    overflow: hidden;
`);

const Container = styled.div`
    background-color: #eee;
`;

const Video = ({url, title}) => {
    const [show, setShow] = React.useState(false);
    const [height, setHeight] = React.useState(0);
    const ref = React.useRef();

    const animation = useSpring({
        height: show ? height : 0,
    });

    React.useLayoutEffect(() => {
        setHeight(ref.current.getBoundingClientRect().height);
    }, [height]);

    if(url.includes('youtube')) url = url.replace('watch?v=', 'embed/');

    return (
        <>
            <Container>
                <Title show={show} onClick={() => setShow(state => !state)}><Icon style={{marginRight: 8}}>movie</Icon>{title}</Title>
                <Wrapper style={animation}>
                    {
                        url.includes('youtube') ?
                            <iframe title={uniqueId()} ref={ref} width={'100%'} height={300} src={url} frameBorder={"0"}
                                    allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                                    allowFullScreen></iframe> :
                            <Movie ref={ref} src={url} controls />
                    }
                </Wrapper>

            </Container>
        </>
    )
};

export default Video;