import Text from "../Atomics/Text";
import React from "react";
import Atomic from "../Atomics/Atomic";
import Quote from "../Atomics/Quote";

const exerciseBlockRenderer = (contentBlock) => {
    const type = contentBlock.getType();

    switch(type) {
        case 'unstyled':
            return {
                component: ({block}) => {
                    return (
                        <Text>
                            {block.getText()}
                        </Text>
                    )
                },
                editable: false,
            }
        case 'quote':
            return {
                component: ({block}) => {
                    return (
                        <Quote>
                            {block.getText()}
                        </Quote>
                    )
                },
                editable: false,
            }
        case 'atomic':
            return {
                component: (props) => (
                    <Atomic {...props} />
                ),
                editable: false,
            }
        default:
            return null;
    };
};

const useExerciseBlockRenderer = () => {
    return exerciseBlockRenderer;
};

export default useExerciseBlockRenderer;