import React from 'react';
import {Editor, EditorState, convertFromRaw, DefaultDraftBlockRenderMap} from 'draft-js';
import useExerciseBlockRenderer from "./ExerciseBlockRenderer";
import Immutable from 'immutable';
import Section from "../Atomics/Section";

const Renderer = ({title, data}) => {
    const editorState = EditorState.createWithContent(convertFromRaw(data));

    const blockRenderer = useExerciseBlockRenderer();

    const blockRenderMap = Immutable.Map({
        'atomic': {
            element: 'div'
        }
    });

    const extendedBlockRenderMap = DefaultDraftBlockRenderMap.merge(blockRenderMap);

    return (
        <Section title={title}>
            <Editor editorState={editorState}
                    blockRendererFn={blockRenderer}
                    blockRenderMap={extendedBlockRenderMap}
                    readOnly={true}/>
        </Section>
    )
};

export default Renderer;