import React from 'react';
import {useGetData} from "../../Providers/CourseProvider";
import {useParams} from 'react-router-dom';
import Renderer from "../../Editor/Renderer";

const Exercise = () => {
    const {exerciseId} = useParams();
    const exercise = useGetData('Exercise', exerciseId);

    return (
        exercise.content ? <Renderer data={exercise.content} title={exercise.title} /> : null
    )
};

export default Exercise;